.App {
  font-family: sans-serif;
  text-align: center;
}

.input-style {
  margin: 50px;
  padding: 10px;
  width: 300px;
  border-radius: 5px;
  border: 1px grey solid;
}

.input-style:focus {
  outline: none;
}

.btn-style {
  width: 100px;
  height: 40px;
  border: 1px grey solid;
  border-radius: 10px;
  background: black;
  color: white;
  font-size: 16px;
  margin: 0px 50px;
}
.btn-style:hover {
  cursor: pointer;
  box-shadow: 3px 3px 10px black;
}

.btn2-style {
  width: 200px;
  height: 40px;
  border: 1px grey solid;
  border-radius: 10px;
  background: black;
  color: white;
  font-size: 16px;
  margin: 0px 50px;
}

.btn-style:focus {
  outline: none;
}

.btn2-style:hover {
  cursor: pointer;
  box-shadow: 3px 3px 10px black;
}

.btn2-style:focus {
  outline: none;
}

.left-todos {
  margin: 35px 50px;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 1px;
}

.checkbox-style {
  margin-right: 20px;
  margin-left: 50px;
}

.todo-style {
  margin: 20px 40px 0 0;
  border: none;
  letter-spacing: 1px;
  font-size: 16px;
}

.delete-style {
  background: black;
  color: white;
  border: 1px grey solid;
  border-radius: 5px;
  padding: 5px 15px;
}

.delete-style:focus {
  outline: none;
}
.delete-style:hover {
  cursor: pointer;
  box-shadow: 3px 3px 10px black;
}
